html,
body {
  margin: 0;
  padding: 0;
}

#one-click {
  background: white;
  min-width: 100vw;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  overflow: hidden;
}

#one-click:has(.MuiSlider-dragging) {
  cursor: grabbing;
}

.status-text {
  position: absolute;
  bottom: 15px;
  right: 15px;
  padding: 12px 30px;
  z-index: 5;
  font-size: 22px;
  font-weight: 500;
  font-family: "Roboto Mono";
  text-align: center;
  max-width: 12vw;
  border-radius: 30px;
  background-color: rgba(58, 55, 55, 0.555);
}

#remote-warning {
  color: #ff9e69;
  font-size: 16px;
  display: block;
  line-height: 85%;
}

#main-view {
  flex: 1;
  position: relative;
  display: flex;
}

#slides-container {
  flex: 1;
  padding: 0;
  display: flex;
  justify-content: center;
}

#slide-deck {
  height: 100%;
  flex: 1;
}

#curr-poll-indicator-container {
  --ratio: calc(16 / 9);
  height: min(100%, calc(100vw / var(--ratio)));
  max-width: 100%;
  aspect-ratio: var(--ratio);
  position: absolute;
  z-index: 1;
  align-self: center;
  container-type: size;
}

#curr-poll-number-completed {
  font-size: 8cqh;
  text-align: center;
  width: 10cqh;
  float: right;
  font-weight: 700;
  letter-spacing: -3px;
  transform: translate(-6.5cqh, 2cqh) rotate(10deg);
}

.progress-bars {
  z-index: 2;
  padding-top: 10px;
  width: calc(100% - 24px);
  align-content: center;
  display: flex;
}

.slides-controls {
  background-color: #212121;
  min-height: 54px;
  max-height: 96px;
  height: 8vh;
}

.ctrl-bar-outer-container {
  width: 100%;
  margin: auto;
  height: 100%;
  padding-top: 5px;
}

.ctrl-bar-inner-container {
  position: relative;
  height: 100%;
}

.ctrl-bar-center {
  display: inline-flex;
  position: absolute;
  left: 50%;
  top: 50%;
  transform: translate(-50%, -50%);
}

.ctrl-bar-right {
  display: inline-flex;
  position: absolute;
  right: 0;
  top: 50%;
  transform: translate(0%, -50%);
  height: 100%;
}

.ctrl-bar-text {
  display: flex;
}

.ctrl-bar-text > p {
  margin: auto;
  font-size: clamp(15px, 1.5vh, 20px);
  font-weight: 400;
  font-family: "Inter";
  color: white;
}

.ctrl-bar-icon-btn {
  color: white;
  padding-top: 6px;

  &:hover {
    cursor: pointer;
    color: lightgray;
  }

  &:focus {
    color: lightgray;
    outline: 1px solid gray;
    outline-offset: -3px;
  }

  &:active {
    color: gray !important;
    outline: none;
  }

  &:focus:not(:focus-visible):not(:hover) {
    color: white;
  }

  &:is(:focus:not(:focus-visible), :hover) {
    color: lightgray;
    outline: none;
  }
}

.ctrl-bar-player-status-container {
  width: 22.5vw;
  display: inline-flex;
  justify-content: flex-end;
  margin-right: 40px;
}

.ctrl-bar-player-status-icon {
  align-content: center;
  margin-top: 6px;
  margin-right: 10px;
}

.ctrl-bar-responsive-icon {
  font-size: clamp(23px, 2.5vh, 28px);
}

.toggle-fullscreen-btn-container {
  height: 100%;
  margin-left: 20px;
  margin-right: 16px;
  align-content: center;
}

.tailspin-loader {
  align-items: center;
  justify-content: center;
  min-width: 100%;
  min-height: 100%;
  background-color: rgb(20, 20, 20);
}

#poll-graph-container {
  background: white;
  position: absolute;
  top: 0;
  left: 0;
  z-index: 1;
  width: 100%;
  height: 100%;
  padding: 0;
  display: flex;
  justify-content: center;
  align-items: center;
}

.thumbnail-caption {
  text-align: center;
  font-size: 16px;
  font-family: "Roboto Mono";
  font-weight: bold;
  margin: 10px 0px;
}

#gameplay {
  background: #212121;
  position: absolute;
  width: 100%;
  height: 100%;
  display: flex;
  justify-content: center;
  align-items: center;
  z-index: 1;
}

#gameplay-container {
  display: flex;
}

#game {
  width: 1024px;
  height: 615px; /* 15px extra needed to display full game height (600px) */
  border: 0;
  align-self: flex-end;
}

#game-paused {
  background-color: rbga(33, 33, 33, 0.25);
  position: absolute;
  width: 1024px;
  height: 600px;
  align-self: flex-end;
  display: flex;
  justify-content: center;
  align-items: center;
}

#game-pause-btn {
  position: absolute;
  left: 24px;
  bottom: 8px;
  width: 285px;
  height: 42px;
  font-size: 20px;
  text-transform: capitalize;
  font-family: "Roboto Mono";
  font-weight: bold;
  letter-spacing: 120%;
}

#game-play-btn {
  background-color: #212121b3; /* opacity 0.7 */
  color: "#00E676";
  align-self: center;
  width: 350px;
  height: 64px;
  font-size: 24px;
  font-family: "Roboto Mono";
  font-weight: bold;
  text-transform: capitalize;
}

#game-timer {
  background-color: #212121;
  color: white;
  font-family: "Inter";
  font-weight: bold;
  font-size: 64px;
  width: 256px;
  height: 204px;
  padding-top: 28px;
  text-align: center;
}

#game-instructions {
  color: white;
  background-color: #212121;
  line-height: 150%;
  font-size: 18px;
  white-space: pre-wrap;
  float: right;
  width: 190px;
  height: 300px;
  padding: 34px;
}

.column {
  display: flex;
  align-self: flex-end;
  flex-direction: column;
  flex: 1;
}

.hide {
  z-index: -1;
}
